:root {
  --font-text: "League Spartan", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-text);
}

.container {
  max-width: 87.5rem;
  margin: 0 auto;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

body {
  background-size: cover;
  height: 100vh;
  background-color: red;
}